import "babel-polyfill";
import './style.css';
import M from 'materialize-css';

window.addEventListener('load', ()=>{
    foodApp.fetchFood("http://temp.dash.zeta.in/food.php")
});


let foodApp = (()=> {   
    
    let cart = [];
    let count = 0;
    
    let fetchFood = async (url) => {
        let response = await fetch(url).catch(err=>console.log(err));
        let foodData = await response.json();
        // console.log(foodData.recipes); 
        
        
         let recipeGrid = foodData.recipes.map((item, index)=>{
                return `
                <div class="col s12 m6" data-key=${index}>
                <div class="card">
                  <div class="card-image">
                    <img src=${item.image}>
                  </div>
                  <div class="card-action">
                    <div style="width: 50%" class="item_detail"> 
                        <p class="" title=${item.name}>${item.name}</p>
                        <p class="">&#8377; ${item.price}</p>
                       
                    </div>
                    <div class="item_btn">
                        <button class='btn btn_add_cart' onclick='foodApp.addToCart("${item.name}","${item.image}","${item.price}","${item.category}","${item.rating}","${item.details}","${item.reviews}")'>ADD TO BAG</button>
                    </div>
                  </div>
                </div>
              </div>           
                `;
         });

         document.getElementById('recipes_card').innerHTML = recipeGrid.join('');

         let favourites = foodData.recipes.filter(item => {
             return item.isFavourite;
         })

         let favouriteGrid = favourites.map((item, index)=>{
            //  console.log(item)

            let details = {
                    name:item.name,
                    item:item.image,
                    price:item.price,
                    cat:item.category,
                    rating:item.rating, 
                    details:item.details,
                    reviews:item.reviews,
            }

            return `
            <div class="col m6 card_content">
            <div class="card">
              <div class="card-image">
                <img src=${item.image}>
              </div>
              <div class="card-action">
                <div style="width: 50%" class="item_detail"> 
                    <p class="truncate" title=${item.name}>${item.name}</p>
                    <p class="">&#8377; ${item.price}</p>
                </div>
                <div class="item_btn">
                   <button class='btn btn_add_cart' onclick='foodApp.addToCart(${details})'>ADD TO BAG</button>
                </div>
              </div>
            </div>
          </div>           
            `;
     });

     document.getElementById('favourite_holder').innerHTML = favouriteGrid.join('');         
    };

  
    const addToCart = (name, item, price, cat, rating, details, reviews)=>{

        for(let i in cart){
            if(cart[i].name === name){
                cart[i].count+=count;
                return;
            } 
        }
    
        let itemObj = {
            name,
            item,
            price,
            cat,
            rating, 
            details,
            reviews
        };
        cart.push(itemObj);
        console.log(item)
    };


    const removeItemFromCart = (name)=>{
        for(let i in cart){
            if(cart[i].name === name){
                cart[i].count--;
                if(cart[i].count === 0) {
                    cart.splice(i,1);
                    break;
                }
            }
        }
    };

    const clearCart = ()=> cart.length=0;


    document.querySelector('#recipes_card').addEventListener('click', (e)=>{
        renderDetailView(e)
    });


    document.querySelector('#favourite_holder').addEventListener('click', (e)=>{
        renderDetailView(e)
    });


    const renderDetailView = (e) => {
        
        if(e.target.nodeName === 'IMG') {
            
            let parentEle = e.target.parentElement.parentElement.parentElement; 
             
             console.log(e.target.parentElement.parentElement.parentElement.children[0].lastElementChild.childNodes)
 
             document.querySelector('#food_detail_container').style.display = 'block';
             document.querySelector('#food_item_container').style.display = 'none';
 
             document.querySelector('#food_detail_container .container').innerHTML = `
             
             <div class="col m6 card_content">
             <div class="card">
               <div class="card-image">
                 <img src=${e.target.src}>
               </div>
               <div class="card-action">
                 <div style="width: 50%" class="item_detail"> 
                     <p class="truncate">${e.target.parentElement.parentElement.parentElement.children[0].lastElementChild.childNodes[1].children[0].innerHTML}</p>
                     <p class="">${e.target.parentElement.parentElement.parentElement.children[0].lastElementChild.childNodes[1].children[1].innerHTML}</p>
                 </div>
                 <div class="item_btn">
                     <button class='btn btn_add_cart' onclick="increment()">+</button>
                     ${count}
                     <button class='btn btn_add_cart' onclick="decrement()">-</button>
                 </div>
               </div>
             </div>
           </div>           
             
             `        }
    };


    const increment = ()=>{

    }


    
    
    //reveal functions
    const returnObject = {
        fetchFood: fetchFood,
        addToCart: addToCart,
        removeItemFromCart: removeItemFromCart,
        clearCart: clearCart
    }

   return returnObject;

})();